.gallery__gradient {
    background: linear-gradient( var(--background-lightpink) 0%, var(--background-lightpink) 30%, var(--background-white) 30%, var(--background-white) 100%);
}

.gallery__position {
    position: relative;

    padding: 0;
    height: 202px;
    width: 320px;
    overflow: hidden;

    @media screen and (min-width: 768px){
        height: 385px;
        width: 768px;
    }

    @media screen and (min-width: 1280px){
        height: 553px;
        width: 1087px;
    }
}

.gallery__image {
    position: absolute;

    width: 40px;
    height: 40px;
    border-radius: 10px;

    @media screen and (min-width: 768px){
        width: 90px;
        height: 90px;
        border-radius: 20px;
    }

    @media screen and (min-width: 1280px){
        width: 150px;
        height: 150px;
        border-radius: 40px;
    }
}

.gallery__image-green {
    top: 50px;
    left: 10px;

    @media screen and (min-width: 768px){
